import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const HeaderMenu = ({ items }) => (

    <ul className="menu">
        {items.map(({ name, link },index) => (
            <li key={index}><Link
                
                to={link}
            >
                {name}
            </Link>
            </li>))
        }
    </ul>

)

HeaderMenu.propTypes = {
    items: PropTypes.array,
}

export default HeaderMenu